export async function onResize() {
    let sp = document.getElementById('footer-spacing');
    if (sp == null || typeof (sp) == "undefined")
        return;
    console.log("Queueing footer callbacks.");
    window.requestAnimationFrame(phase1);
}
function phase1() {
    let sp = document.getElementById('footer-spacing');
    sp.style.height = "0px";
    console.log("Zeroing footer divider.");
    window.requestAnimationFrame(phase2);
}
function phase2() {
    let footer = document.getElementById('footer-block');
    if (footer == null || typeof (footer) == "undefined")
        return;
    let text = document.getElementById('textarea');
    if (text == null || typeof (text) == "undefined")
        return;
    let sp = document.getElementById('footer-spacing');
    let needed = text.scrollHeight - sp.offsetHeight;
    let available = document.body.clientHeight - 128;
    let space = available - needed;
    //console.log("Height diff = "+diff);
    sp.style.height = (space < 0 ? 75 : space) + "px";
    console.log("Setting divider size to " + sp.style.height + " because space = " + space + " (" + needed + "/" + available + ") from " + text.scrollHeight + "-" + sp.offsetHeight);
}
